#slider {
  position: relative;
  background-color: $secondaryColor;

  & .container-full {
    width: 100%;
    padding-top: 320px;
    padding-bottom: 120px;

    background-color: $secondaryColor;

    @media (max-width: $mobile) {
      padding-top: 300px;
    }
  }

  & h3.title-h2 {
    max-width: 740px;

    @media (max-width: $mobile) {
      max-width: 302px;
    }
  }
}

.slider__contacts {
  margin-top: 60px;
}

.slider__contacts-left {
  display: flex;

  @media (max-width: $mobile-big) {
    flex-direction: column;
  }
}

.slider__contacts-left > * + * {
  margin-left: 112px;

  @media (max-width: $mobile-big) {
    margin-left: unset;
    margin-top: 35px;
    
  }
}

.slider__contacts-links > * + * {
  margin-top: 24px;

  
}

.links-item {
  display: flex;
  padding-left: 170px;
  position: relative;
  align-items: center;

  & span {
    position: absolute;
    left: 0;

    font-weight: 600;
    font-size: 32px;
    line-height: 1.3;
    color: #9ccaff;

    @media (max-width: $mobile) {
      font-size: 18px;
    }
  }

  & a {
    color: $primaryColor;
  }

  @media (max-width: $mobile-big) {
    font-size: 28px;
  }

  @media (max-width: $mobile) {
    font-size: 18px;
    padding-left: 80px;
  }
}

.links-item > * + * {
  margin-left: 56px;

  @media (max-width: $mobile) {
    margin-left: 30px;
  }
}

.slider__contacts-socials{
  display: flex;
  flex-direction: column;

  @media (max-width: $mobile) {
    flex-direction: row;
    align-items: center;
  }
}

.slider__contacts-socials > * + * {
  margin-top: 26px;

  @media (max-width: $mobile) {
    margin-top: 0;
    margin-left: 40px;
  }
}

.socials-item {
  display: flex;
  align-items: center;

  color: $primaryColor;

  @media (max-width: $mobile) {
    font-size: 18px;
  }
}

.socials-item > * + * {
  margin-left: 16px;

  @media (max-width: $mobile) {
    margin-left: 10px;
  }
}

.slider__contacts-right {
  margin-top: 95px;
}

.swiper-wrapper {
  align-items: center;
}
