#header {
  background-image: url(../images/header/header-bg.jpg);
  background-repeat: no-repeat;
  background-position: center;
  
  background-color: #4690ff;

  min-height: 2580px;

  text-align: center;

  & .header__wrapper{
    @media (max-width: $laptop) {
      height: 87%;
    }

    @media (max-width: $mobile) {
      height: 94%;
    }
  }

  @media (max-width: $laptop) {
    min-height: unset;
  }

  @media (max-width: $mobile) {
    min-height: unset;
    background-image: url(../images/header/header-bg-mob.jpg);
    background-position: top;
  }

  
}

.header__logo img{

  @media (max-width: $mobile) {
    margin: 0 auto;
    width: 115px;
  }
}

.header__nav {
  padding-top: 40px;
  padding-bottom: 40px;

  @media (max-width: $mobile) {
    padding-top: 24px;
    padding-bottom: 24px;
  }
}

.header__top {
  margin: 0 auto;
  max-width: 812px;
}

.header__wrapper {
  padding-top: 50px;
  height: 100%;

  display: flex;
  flex-direction: column;
  justify-content: space-between;

  @media (max-width: $mobile) {
    padding-top: 0;
  }
}

.header__link {
  margin: 0 auto;
  margin-top: 40px;

  display: flex;
  flex-direction: column;
  align-items: center;

  max-width: 640px;

  & img {
    margin: 0 auto;

    
  }

  & img:first-child{
    width: 40px;
  }
  

  & a{
    @media (max-width: $laptop) {
      width: 130px;
    }

    @media (max-width: $mobile) {
      width: 100px;
    }
  }

  @media (max-width: $laptop) {
    font-size: 25px;
  }

  @media (max-width: $mobile) {
    font-size: 18px;
    margin-top: 14px;
  }
}

.header__link a > * + * {
  margin-top: 40px;

  @media (max-width: $mobile) {
    margin-top: 14px;
  }
}

.header__link > * + * {
  margin-top: 40px;

  @media (max-width: $mobile) {
    margin-top: 14px;
  }
}

.header__bottom {
  margin: 0 auto;
  max-width: 730px;
  text-align: left;

  & span {
    color: #ffea31;
  }

  & p {
    max-width: 640px;
  }

  @media (max-width: $mobile) {
    max-width: 320px;
    font-size: 18px;
  }
}

.header__content {
  height: 2250px;

  & .container {
    height: 100%;
  }

  @media (max-width: $mobile) {
    height: 1140px;
  }
}

.header__bottom > * + * {
  margin-top: 30px;
}
