#partners {
  position: relative;
  background-color: $bg-color;

  color: $thirdColor;

  & .container-full {
    width: 100%;
    padding-top: 120px;
    padding-bottom: 120px;
    border-top-left-radius: 60px;
    border-top-right-radius: 60px;
    background-color: $bg-color;

    margin-top: -50px;

    @media (max-width: $laptop) {
      padding-top: 70px;
    }

    @media (max-width: $mobile) {
      padding-top: 40px;
    }
  }
}

#partners .wrapper__container {
}

.partners__wrapper {
  & h2 {
    max-width: 740px;
  }

  & span {
    color: $secondaryColor;
  }

  & > * + * {
    margin-top: 40px;
  }
}

.partners__bg {
  position: absolute;
  top: 0;
  z-index: 5;

  margin-top: 0;

  @media (max-width: $desktop-small) {
    display: none;
  }
}

.partners__fonds {
  position: relative;
  z-index: 10;

  & .title-h2 {
    max-width: 640px;
    margin-bottom: 60px;
  }

  & h2.title-h2 {
    max-width: 740px;
    margin-bottom: 40px;
  }
}

.partners__img-mob{
  display: none;
  margin-left: auto;
  margin-right: auto;

  & img{
    margin-left: auto;
  margin-right: auto;
  }


  @media (max-width: $mobile) {
    display: block;
    
  }
}

.partners__img{
  display: block;

  @media (max-width: $mobile) {
    display: none;
  }
}

.partners__fonds-wrapper {
  margin-top: 60px;
  display: flex;
  justify-content: space-between;

  @media (max-width: $mobile-big) {
    flex-direction: column;

    & > * + * {
      margin-top: 35px;
    }
  }
}

.partners-item {
  max-width: 510px;

  display: flex;
  align-items: center;

  font-weight: 600;
  font-size: 24px;
  line-height: 1.3;

  & > * + * {
    margin-left: 40px;
  }

  @media (max-width: $mobile) {
    font-size: 18px;
  }
}

.partners__fonds-left > * + * {
  margin-top: 30px;
}

.partners__fonds-right > * + * {
  margin-top: 30px;
}

.parnters__fonds-button {
  margin-top: 60px;

  @media (max-width: $mobile) {
    margin-top: 30px;
  }
}
