#timeline {
  position: relative;
  border-top-left-radius: 60px;
  border-top-right-radius: 60px;
  border-top-left-radius: 60px;
  border-top-right-radius: 60px;
  min-height: 2970px;

  color: $thirdColor;

  background-image: url(../images/timeline/timeline-bg.png);
  background-repeat: no-repeat;
  background-position: bottom;

  margin-bottom: -252px;
  z-index: 5;

  & .container-full {
    width: 100%;

    padding-bottom: 120px;

    margin-top: -50px;

    z-index: 5;

    @media (max-width: $mobile) {
      margin-top: -40px;
    }
  }

  & .color-container {
    padding-top: 120px;
    border-top-left-radius: 60px;
    border-top-right-radius: 60px;
    background-color: $bg-color;

    @media (max-width: $laptop) {
      padding-top: 70px;
    }

    @media (max-width: $mobile) {
      padding-top: 40px;
    }
  }

  & .wrapper__container {
    position: relative;
    z-index: 10;
  }

  @media (max-width: $laptop) {
    min-height: 2820px;
  }

  @media (max-width: $mobile-big) {
    min-height: 2700px;
  }

  @media (max-width: $mobile) {
    min-height: 1770px;

    background-image: url(../images/timeline/timeline-bg-mob.png);
 
  background-position: bottom;
  }
}

.timeline__header {
  max-width: 525px;
  margin-left: auto;
  margin-right: auto;

  & > * + * {
    margin-top: 30px;
  }

  @media (max-width: $mobile) {
    font-size: 18px;
  }
}

.timeline__img {
  margin-top: 20px;
  display: block;

  @media (max-width: $mobile) {
    display: none;
  }
}

.timeline__img-mob{
  margin-top: 10px;
  display: none;

  & img{
    margin-left: auto;
    margin-right: auto;
  }

  @media (max-width: $mobile) {
    display: block;
  }
}

.timeline__info {
  max-width: 640px;
  margin-top: 60px;

  margin-left: auto;
  margin-right: auto;

  @media (max-width: $mobile) {
    font-size: 18px;
    margin-top: 30px;
  }
}

.timeline__steps {
  margin-top: 120px;

  max-width: 640px;
  margin-left: auto;
  margin-right: auto;

  @media (max-width: $mobile-big) {
    margin-top: 70px;
  }

  @media (max-width: $mobile) {
    margin-top: 40px;
  }

  & h3{
    @media (max-width: $mobile) {
    max-width: 300px;
    }
  }

  
}

.timeline__sequence {
  margin-top: 60px;
}

.timeline__sequence > * + * {
  margin-top: 60px;
}

.item-step__info {
  & span {
    font-weight: 700;
    font-size: 56px;
    line-height: 1.05;

    color: $secondaryColor;

    @media (max-width: $mobile) {
      font-size: 22px;
    }
  }

  & p {
    font-weight: 600;
    font-size: 32px;
    line-height: 1.3;

    @media (max-width: $mobile) {
      font-size: 18px;
    }
  }
}

.item-step__img {
  flex-shrink: 0;
}

.item-step__info > * + * {
  margin-top: 25px;

  @media (max-width: $mobile) {
    margin-top: 14px;
  }
}

.item-step {
  display: flex;
}

.item-step > * + * {
  margin-left: 50px;

  @media (max-width: $mobile) {
    margin-left: 16px;
  }
}

.timeline__bg {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);

  z-index: 4;
}
